body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.navbar-top {
    position: sticky;
    top: -13px;
    z-index: 1;
}

.navbar-brand img {
    width: 50px;
    border-radius: 100px;
    height: auto;
    margin-right: 10px;
    transition: cubic-bezier(0.23, 1, 0.320, 1) .4s;
    box-shadow: 0.5px 0.2px 5px rgba(0, 0, 0, 0.541);
}

.navbar-brand img:hover {
    width: 51px;
    height: auto;
    margin-right: 15px;
    border-radius: 80px 80px 60px 75px;
    box-shadow: -2px -2px 5px rgba(32, 31, 31, 0.76);
}

.navbar-brand {
    width: 10%;
    height: 50px;
    /* border: 1px solid black; */
}

.card img {
    width: 100%;
    aspect-ratio: 1/0.9;
    object-fit: cover;
}

.card-text {
    width: 100%;
    aspect-ratio: 1/0.4;
    object-fit: cover;
}

.student_heading {
    background: rgba(6, 158, 6, 0.589);
    padding: 12px;
    font-size: 30px;
    border-radius: 10px;
    color: white;
    width: 35%;
    text-align: center;
    position: sticky;
    top: -6px;
    z-index: 2;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1), .5s;
}

.student_heading:hover {
    color: rgba(0, 0, 0, 0.74);
    background: rgba(2, 206, 2, 0.747);
}

.p-card,
.card {
    transition: cubic-bezier(0.075, 0.82, 0.165, 1), .5s;
}

.p-card:hover .card {
    margin-left: 10px;
}